import React, { useState } from 'react';

function EPQCalculator() {
  const [demand, setDemand] = useState('');          // D: Demanda anual
  const [productionRate, setProductionRate] = useState('');  // P: Produccin anual
  const [productionCost, setProductionCost] = useState('');  // C: Costo de produccin
  const [orderingCost, setOrderingCost] = useState('');      // Co: Costo de preparacin/pedido
  const [workingDays, setWorkingDays] = useState('');        // DH: Das hbiles
  const [holdingCost, setHoldingCost] = useState('');        // Ch: Costo de retencin
  const [interestRate, setInterestRate] = useState('');      // I: Inters en porcentaje

  const [useInterest, setUseInterest] = useState(false);     // Opcin entre usar Ch o I

  const [results, setResults] = useState(null);

  const calculateEPQ = () => {
    if (demand && productionRate && productionCost && orderingCost && workingDays) {
      // Validar si se ha ingresado el costo de retencin (Ch) o el inters (I)
      let Ch = holdingCost;
      if (useInterest) {
        if (!interestRate) {
          alert('Debe ingresar el inters cuando se selecciona usar el inters.');
          return;
        }
        Ch = (interestRate / 100) * productionCost;  // Calcular Ch usando el inters (I)
      }

      if (!Ch) {
        alert('Debe ingresar el costo de retencin (Ch) o seleccionar usar el inters.');
        return;
      }

      if (productionRate <= demand) {
        alert('La tasa de produccin (P) debe ser mayor que la demanda (D).');
        return;
      }

      // Frmula para EPQ (Q*)
      const Q = Math.sqrt((2 * demand * orderingCost) / (Ch * (1 - demand / productionRate)));

      // Clculo del tiempo de ciclo (T)
      const T = workingDays / (demand / Q);

      // Produccin diaria (p)
      const p = productionRate / workingDays;

      // Tiempo de duracin de la fase de produccin (t)
      const t = Q / p;

      // Costo anual de retencin
      const holdingCostAnnual = 0.5 * (1 - demand / productionRate) * Q * Ch;

      // Costo anual de preparacin
      const preparationCostAnnual = (demand / Q) * orderingCost;

      // Costo total anual (TC)
      const totalCost = preparationCostAnnual + holdingCostAnnual;

      setResults({
        Q: Q.toFixed(2),
        T: T.toFixed(2),
        p: p.toFixed(2),
        t: t.toFixed(2),
        holdingCostAnnual: holdingCostAnnual.toFixed(2),
        preparationCostAnnual: preparationCostAnnual.toFixed(2),
        totalCost: totalCost.toFixed(2),
      });
    } else {
      alert('Por favor, ingrese todos los valores requeridos.');
    }
  };

  return (
    <div style={{ padding: '20px', textAlign: 'center' }}>
      <h2>Modelo de Lote de Produccin (EPQ)</h2>
      <div>
        <label>
          Demanda Anual (D): 
          <input
            type="number"
            value={demand}
            onChange={(e) => setDemand(e.target.value)}
            placeholder="Ingrese la demanda anual"
            style={{ margin: '10px' }}
          />
        </label>
      </div>
      <div>
        <label>
          Produccin Anual (P): 
          <input
            type="number"
            value={productionRate}
            onChange={(e) => setProductionRate(e.target.value)}
            placeholder="Ingrese la produccin anual"
            style={{ margin: '10px' }}
          />
        </label>
      </div>
      <div>
        <label>
          Costo de Produccin (C): 
          <input
            type="number"
            value={productionCost}
            onChange={(e) => setProductionCost(e.target.value)}
            placeholder="Ingrese el costo de produccin"
            style={{ margin: '10px' }}
          />
        </label>
      </div>
      <div>
        <label>
          Costo de Preparacin (Co): 
          <input
            type="number"
            value={orderingCost}
            onChange={(e) => setOrderingCost(e.target.value)}
            placeholder="Ingrese el costo de preparacin"
            style={{ margin: '10px' }}
          />
        </label>
      </div>
      <div>
        <label>
          Das Hbiles (DH): 
          <input
            type="number"
            value={workingDays}
            onChange={(e) => setWorkingDays(e.target.value)}
            placeholder="Ingrese los das hbiles"
            style={{ margin: '10px' }}
          />
        </label>
      </div>

      {/* Seleccin entre Ch o Inters */}
      <div style={{ margin: '10px' }}>
        <label>
          <input
            type="radio"
            checked={!useInterest}
            onChange={() => setUseInterest(false)}
          />
          Ingresar Costo de Retencin (Ch)
        </label>
        <label style={{ marginLeft: '20px' }}>
          <input
            type="radio"
            checked={useInterest}
            onChange={() => setUseInterest(true)}
          />
          Usar Inters (I)
        </label>
      </div>

      {!useInterest && (
        <div>
          <label>
            Costo de Retencin (Ch): 
            <input
              type="number"
              value={holdingCost}
              onChange={(e) => setHoldingCost(e.target.value)}
              placeholder="Ingrese el costo de retencin"
              style={{ margin: '10px' }}
            />
          </label>
        </div>
      )}

      {useInterest && (
        <div>
          <label>
            Inters (I) [%]: 
            <input
              type="number"
              value={interestRate}
              onChange={(e) => setInterestRate(e.target.value)}
              placeholder="Ingrese el inters (%)"
              style={{ margin: '10px' }}
            />
          </label>
        </div>
      )}

      <button onClick={calculateEPQ} style={{ margin: '10px' }}>Calcular</button>

      {results && (
        <div style={{ marginTop: '20px' }}>
          <h3>Resultados:</h3>
          <p>Tamao del lote de produccin (Q*): {results.Q} unidades</p>
          <p>Tiempo de ciclo (T): {results.T} das</p>
          <p>Produccin diaria (p): {results.p} unidades</p>
          <p>Duracin de la fase de produccin (t): {results.t} das</p>
          <p>Costo anual de retencin: {results.holdingCostAnnual}</p>
          <p>Costo anual de preparacin: {results.preparationCostAnnual}</p>
          <p>Costo total anual (TC): {results.totalCost}</p>
        </div>
      )}
    </div>
  );
}

export default EPQCalculator;
